.otp {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 650px;
  display: flex;
}

.otp-Form {
  background-color: #fff;
  border-radius: 15px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 300px;
  height: 400px;
  padding: 20px 30px;
  display: flex;
  position: relative;
  box-shadow: 0 0 20px #00000015;
}

.mainHeading {
  color: #0f0f0f;
  font-size: 1.1em;
  font-weight: 700;
}

.otpSubheading {
  color: #000;
  text-align: center;
  font-size: .7em;
  line-height: 17px;
}

.inputContainer {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 100%;
  display: flex;
}

.otp-input {
  text-align: center;
  border-radius: #7744d9;
  color: #2c2c2c;
  background-color: #e4e4e4;
  border: none;
  outline: none;
  width: 30px;
  height: 30px;
  font-weight: 600;
}

.otp-input:focus, .otp-input:valid {
  background-color: #7f81ff33;
  transition-duration: .3s;
}

.verifyButton {
  color: #fff;
  cursor: pointer;
  background-color: #7744d9;
  border: none;
  border-radius: 10px;
  width: 100%;
  height: 30px;
  font-weight: 600;
  transition-duration: .2s;
}

.verifyButton:hover {
  background-color: #9091ff;
  transition-duration: .2s;
}

.exitBtn {
  color: #000;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  font-size: 1.1em;
  position: absolute;
  top: 5px;
  right: 5px;
  box-shadow: 0 0 20px #0000002c;
}

.resendNote {
  color: #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  width: 100%;
  font-size: .7em;
  display: flex;
}

.resendBtn {
  color: #7f81ff;
  cursor: pointer;
  background-color: #0000;
  border: none;
  font-size: 1.1em;
  font-weight: 700;
}
